<template>
  <div
    class="widgetContainer widgetContainer--scrollable widgetContainer--center bg-white personDetails">
    <div class="navigationHeader">
      <div class="navigationHeader__inner">
        <span class="icon-Arrow-big-left cancel" @click="backAction" />
        <p class="title">
          {{ $t('kyc_NavTitle') }}
        </p>
      </div>
    </div>

    <div class="widgetContainer__body">
      <el-form
        @keyup.enter.native="submitForm()"
        :model="user"
        :rules="rules.user"
        ref="user"
        @submit.prevent.native
        class="wise-form">
        <el-form-item
          class="is-no-asterisk"
          prop="firstName"
          :label="$t('fName')">
          <el-input v-model="user.firstName" />
        </el-form-item>
        <el-form-item
          class="is-no-asterisk"
          prop="lastName"
          :label="$t('lName')">
          <el-input v-model="user.lastName" />
        </el-form-item>
        <el-form-item class="is-no-asterisk" prop="email" :label="$t('email')">
          <el-input v-model="user.email" type="email" />
        </el-form-item>
        <el-form-item
          class="is-no-asterisk date-of-birth"
          prop="dateOfBirth"
          :label="$t('dob')">
          <el-date-picker
            format="MM-dd-yyyy"
            placeholder="MM-DD-YYYY"
            :picker-options="datePickerOptions"
            :default-value="minDate"
            v-model="user.dateOfBirth"
            value-format="yyyy-MM-dd"
            type="date" />
        </el-form-item>
        <el-form-item class="is-no-asterisk" prop="idNumber" :label="$t(selectedCountry.identityMask.type)">
          <SSNInput :country="country" :value="user.idNumber" @input="ssnChange" :autofocus="false" />
        </el-form-item>
      </el-form>
    </div>
    <div class="personDetails__footer">
      <el-button type="primary" class="el-button__brand brand" @click="submitForm()" :disabled="isFormValid">
        {{ $t('kyc_NavRightButton_Title') }}
      </el-button>
    </div>
  </div>
</template>
<script>
import SSNInput from '@/components/SSNInput';
import { alphaAndSpaceRule } from '@/utils/form';
import { mapActions, mapGetters } from 'vuex';
import { format } from 'date-fns';
import kyc from '../mixins/kyc';
import { getCountryDetailsFromPhoneNumber, getSupportedCountries } from '@/utils/env';

export default {
  name: 'KycPersonDetails',
  mixins: [kyc],
  components: {
    SSNInput
  },
  data() {
    return {
      datePickerOptions: {
        disabledDate(date) {
          const minDate = new Date();
          minDate.setFullYear(minDate.getFullYear() - 21);
          return date > minDate;
        }
      },
      isSsnValid: false,
      user: {
        firstName: '',
        lastName: '',
        email: '',
        dateOfBirth: '',
        idNumber: '',
        idType: 'ssn'
      },
      rules: {
        user: {
          firstName: alphaAndSpaceRule('First Name', true),
          lastName: alphaAndSpaceRule('Last Name', true),
          email: [
            {
              required: true,
              message: 'Email is required',
              trigger: 'blur'
            },
            {
              type: 'email',
              message: 'Please provide a valid email address',
              trigger: 'blur'
            }
          ],
          dateOfBirth: [
            {
              required: true,
              message: 'Date of birth is required'
            }
          ],
          idNumber: [
            {
              required: true,
              message: 'Tax ID is required'
            }
          ]
        }
      }
    };
  },
  computed: {
    ...mapGetters('user', ['getPersonDetails']),
    minDate() {
      const minDate = new Date();
      minDate.setFullYear(minDate.getFullYear() - 21);
      let month = minDate.getMonth() + 1;
      month = ('' + month).length === 1 ? '0' + month : month;
      let date = minDate.getDate();
      date = ('' + date).length === 1 ? '0' + date : date;
      return minDate.getFullYear() + '-' + month + '-' + date;

    },
    isFormValid() {
      if (
        this.user.firstName &&
        this.user.lastName &&
        this.user.email &&
        this.user.dateOfBirth &&
        this.user.idNumber && this.isSsnValid
      ) {
        return false;
      } else {
        return true;
      }
    },
    country() {
      const details = getCountryDetailsFromPhoneNumber(this.getPersonDetails.phone);
      if(details && details.code)
        return details.code;
      
      return 'US';
    },
    selectedCountry() {
      const supportedCountries = getSupportedCountries();
      return supportedCountries.find(c => c.code === this.country);
    },
  },
  created() {
    /** @type {import('@/modules/user/store/state').userState['personDetails']} */
    const person = this.getPersonDetails;
    if (person && person.id) {
      const { firstName, lastName, idNumber, email, dateOfBirth, idType } = person;
      this.isSsnValid = true;
      this.user = {
        firstName,
        lastName,
        idNumber,
        email,
        dateOfBirth,
        idType
      };
    }
  },
  methods: {
    ...mapActions('user', ['updatePerson']),
    backAction() {
      this.goToPreviousScreen();
    },
    ssnChange(v) {
      if(v) {
        this.isSsnValid = v.isValid;
        this.user.idNumber = v.value;
        this.user.idType = v.country.identityMask.type
      } else {
        this.isSsnValid = false;
      }
    },
    submitForm() {
      const person = this.getPersonDetails;

      this.$refs.user.validate((valid) => {
        if (!valid) return;

        /** @type {import('@/modules/user/store/state').userState['personDetails']} */
        if (person && person.id) {
          const loader = this.showLoader();
          const user = this.user;
          if (typeof user.dateOfBirth !== 'string')
            user.dateOfBirth = format(user.dateOfBirth, 'yyyy-MM-dd');
          this.updatePerson({
            personId: person.id,
            person: user
          })
            .then(() => {
              this.$router.push('/kyc/personAddress');
            })
            .catch((e) => {
              console.log(e);
              this.apiErrorMessage(e);
            })
            .finally(() => {
              loader.close();
            });
        }
      });
    }
  }
};
</script>

<style lang="scss">
  .widgetContainer { 
    &.personDetails {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      margin: 0;
      padding-bottom: 40px;
      .navigationHeader {
        min-height: 68px;
      }
      .widgetContainer__body {
        padding: 30px;
        padding-top: 30px;
        position: relative;
      }
      .date-of-birth {
        display: flex;
        flex-direction: column;

        .el-form-item__label {
          margin-right: auto;
        }

        .el-date-editor {
          min-width: 100%;
        }
      }
    }
  }
  .personDetails__footer {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
   background-color: white;
    padding: 16px 30px;
    .el-button {
      width: 100%;
    }
  } 
</style>
